import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import HostelExportCard from "./HostelExportCard";
import { useHostelExportExcelList } from "../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";
const HostelExport = ({ hid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [openStep, setOpenStep] = useState("");
  const {
    hostelExportExcelList,
    hostelExportExcelListLoading,
    hostelExportExcelListRefetch,
  } = useHostelExportExcelList({
    data: {
      hid: hid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !hid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (hostelExportExcelList?.list?.length) {
      let obj = nextPage(hostelExportExcelList?.list?.length);
      setState(() => obj);
    }
  }, [hostelExportExcelList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !hostelExportExcelListLoading &&
      hostelExportExcelList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    hostelExportExcelListRefetch();
  }, [hostelExportExcelListRefetch]);

  const onClose = () => {
    setOpenStep("");
  };
  return (
    <>
      {/* <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("hostel_data_export")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom /> */}

      {/* <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        > */}
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
        </section>
      </div>

      {hostelExportExcelList?.list?.map((excel, index) =>
        hostelExportExcelList?.list?.length === index + 1 ? (
          <div key={excel?._id} ref={ref}>
            <HostelExportCard excel={excel} hid={hid} onRefetch={onRefetch} />
          </div>
        ) : (
          <HostelExportCard
            excel={excel}
            key={excel?._id}
            hid={hid}
            onRefetch={onRefetch}
          />
        )
      )}
      {hostelExportExcelListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
        )
      ) : search ? (
        hostelExportExcelList?.list?.length === 0 && (
          <EmptyMessage
            title="No excel found related this search."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      ) : (
        hostelExportExcelList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No excel added."
          />
        )
      )}
      {/* </UserMemberContentWrapper>
      </UserMemberContainerWrapper> */}
    </>
  );
};

export default HostelExport;
