import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { useCallback } from "react";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import AdmissionExport from "./AdmissionExport";
import HostelExport from "../../HostelManager/HostelExport/HostelExport";
const AdmissionExportConfig = ({ aid, fid, instituteId, hid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const onNavigate = useCallback(() => {
    let url = "";

    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/admission`
        : `/q/${params.username}/member/staff/admission`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.2rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("data_export")}</h6>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/admission?type=export${tab.urlKey}`
                  : `/q/${params.username}/member/staff/admission?type=export${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          {searchParams.get("n") === "hostel" ? (
            <HostelExport
              aid={aid}
              fid={fid}
              instituteId={instituteId}
              hid={hid}
            />
          ) : (
            <AdmissionExport aid={aid} fid={fid} instituteId={instituteId} />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default AdmissionExportConfig;
const tabList = [
  {
    label: "admission",
    id: "admission",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "hostel",
    id: "hostel",
    urlKey: "&n=hostel",
    groupIs: ["hostel"],
  },
];
