import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import FinanceExportEdit from "../../FinanceManager/FinanceExport/FinanceExportEdit";

import {
  useHostelEditExportExcel,
  useHostelRemoveExportExcel,
} from "../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";

const HostelExportCardMenu = ({ onClose, xlsxId, hid, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [hostelRemoveExportExcel] = useHostelRemoveExportExcel();
  const [hostelEditExportExcel] = useHostelEditExportExcel();

  const onDelete = () => {
    if (hid && xlsxId?._id) {
      setDisabled((pre) => !pre);
      hostelRemoveExportExcel({
        hid: hid,
        xlsxId: xlsxId?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (hid && xlsxId?._id) {
      setDisabled((pre) => !pre);
      hostelEditExportExcel({
        hid: hid,
        xlsxId: xlsxId?._id,
        excelUpdate: {
          excel_file_name: val,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return edit ? (
    <FinanceExportEdit
      onClose={onOpenEdit}
      onEditAction={onEdit}
      xlsxId={xlsxId}
    />
  ) : (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onOpenEdit}>{t("edit")}</h6>
      <h6 onClick={onDelete}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default HostelExportCardMenu;
